<template>
<div id="app">
    <el-row class="first-section">
      <el-col :span="18">
        <Question v-bind:question="question"></Question>
      </el-col>
      <el-col :span="5" :offset="1">
        <Donate v-bind:donate="donate" :qid="Number(this.$route.params.id)"></Donate>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="18">
        <div v-for="(answer ,x) in answers" :key="x">
          <Answer v-bind:answer="answer"></Answer>
        </div>
        <Respond v-bind:qid="Number(this.$route.params.id)"></Respond>
      </el-col>
      <el-col :span="6">
        <div v-for="(addOn ,x) in addOns" :key="x">
          <AddOn v-bind:addOn="addOn"></AddOn>
        </div>
      </el-col>
    </el-row>
    <notifications position="top left" classes="noti"/>
</div>
</template>

<script>
import Answer from '../components/product/Answer'
import Question from '../components/product/Question'
import Donate from '../components/product/DonateSection'
import AddOn from '../components/product/AddOn'
import Respond from '../components/product/Respond'
import { url } from '../url'
// import { router } from '../router/index'
export default {
  name: 'product',
  mounted () {
    // console.log(url + 'api/questions/' + this.$route.params.id)
    fetch(url + 'api/questions/' + this.$route.params.id, {
      method: 'get',
      headers: new Headers({
        'Authorization': 'Bearer ' + localStorage.getItem('token'),
        'Content-Type': 'application/json'
      })
    }).then(data => data.json().then(
      data => {
        if (data.win_rule === '') {
          this.total_vote += 1
        }
        this.question = data
      }
    ))
    fetch(url + 'api/questions/' + this.$route.params.id + '/answers', {
      method: 'get',
      headers: new Headers({
        'Content-Type': 'application/json'
      })
    }).then(data => data.json().then(
      async data => {
        var temp = {}
        for (var l = 0; l < data.length; l++) {
          await fetch(url + 'api/questions/' + this.$route.params.id + '/answers/' + String(data[l]), {
            method: 'get',
            headers: new Headers({
              'Content-Type': 'application/json'
            })
          }).then(data => data.json().then(
            data => {
              temp = data
            }
          ))
          await fetch(url + 'api/questions/' + this.$route.params.id + '/answers/' + String(data[l]) + '/vote', {
            method: 'get',
            headers: new Headers({
              'Authorization': 'Bearer ' + localStorage.getItem('token'),
              'Content-Type': 'application/json'
            })
          }).then(data => data.json().then(
            data => {
              if (data.total_star !== '') {
                temp.count = 0
              }
              temp.count = data.total_star
              this.answers.push(temp)
            }
          ))
        }
      }
    ))
    fetch(url + 'api/questions/' + this.$route.params.id + '/donate', {
      method: 'get',
      headers: new Headers({
        'Authorization': 'Bearer ' + localStorage.getItem('token'),
        'Content-Type': 'application/json'
      })
    }).then(data => data.json().then(
      data => {
        this.donate.donateAmount = data.total_amount
        this.donate.remainTime = 30
        this.donate.voteAmount = this.donate.donateAmount / 5
        for (var i = 0; i < data.length; i++) {
          fetch(url + 'api/questions/' + data[i] + '/donate', {
            method: 'get',
            headers: new Headers({
              'Authorization': 'Bearer ' + localStorage.getItem('token'),
              'Content-Type': 'application/json'
            })
          })
        }
      }
    ))
    fetch(url + 'api/questions/' + this.$route.params.id + '/vote', {
      method: 'get',
      headers: new Headers({
        'Authorization': 'Bearer ' + localStorage.getItem('token'),
        'Content-Type': 'application/json'
      })
    }).then(data => {
      data.text().then(
        data => {
          // console.log(data)
          this.total_vote = data.total_star
          this.current_vote = data.voted_star
          this.current_left = this.total_vote - this.current_vote
        }
      )
    }).then(
      this.$notify({
        title: '已投票' + this.current_vote + '，還剩' + this.current_left,
        duration: 2500
      })
    )
  },
  components: {
    Answer,
    Question,
    Donate,
    AddOn,
    Respond
  },
  data () {
    return {
      addOns: [{
        addOnAmount: 30,
        addOnDes: '意識不僅存在於大腦中也有可能在宇宙中存在；兩個質子間會互相傳輸能量影響彼此，根據熱力學第二定律的推理，不同溫差的物質會使能量達到平衡，此為量子糾纏。意識很可能是量子糾纏形成的聯結。宇宙間每個粒子在大霹靂時都存在過量子糾纏的狀態，一旦某個外因的刺激仍有可能重建量子糾纏關係，因此人工智慧複製人類思維，使碳基生命進化為矽基生命有待驗證。'
      }],
      donate: {
        donateAmount: 0,
        voteAmount: 0,
        remainTime: 24
      },
      question: {
      },
      answers: [],
      total_vote: 0,
      current_vote: 0,
      current_left: 0
    }
  },
  methods: {
    makeDonateCard () {
      fetch(url + 'api/questions/' + this.$route.params.id + '/donate', {
        method: 'post',
        headers: new Headers({
          'Authorization': 'Bearer ' + localStorage.getItem('token'),
          'Content-Type': 'application/json'
        }),
        body: this.card
      }).catch(
        // function (error) {
        //   alert(error)
        //   // console.log(error)
        // }
      )
    }
  }
}
</script>
